import React, { useEffect, useState } from "react";
import { store } from "../../../store";
import LogoText from "../../../assets/images/text-logo.jpg";
import AnchorLink from "react-anchor-link-smooth-scroll";

export const TopBlock = () => {
  const [lang, setLang] = useState("");
  const currentLang = store.getState().lang;

  useEffect(() => {
    setLang(currentLang);
  }, [currentLang]);
  return (
    <div className="sigma-banner banner-2">
      <div className="sigma_banner-slider">
        <div className="sigma_banner-slider-inner bg-cover bg-center">
          <div className="container">
            <div className="intro_text">
              <p></p>
              <img src={LogoText} />
              <p></p>
              <AnchorLink href="#shop" className="btn shop-btn">
                {lang === "ru" ? "Просмотр" : "Дивитися"}
              </AnchorLink>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
