import { createStore } from "redux";
import { persistStore, persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";

/**
 * This is a reducer - a function that takes a current state value and an
 * action object describing "what happened", and returns a new state value.
 * A reducer's function signature is: (state, action) => newState
 *
 * The Redux state should contain only plain JS objects, arrays, and primitives.
 * The root state value is usually an object.  It's important that you should
 * not mutate the state object, but return a new object if the state changes.
 *
 * You can use any conditional logic you want in a reducer. In this example,
 * we use a switch statement, but it's not required.
 */

const initialState = {
  basket: [],
  lang: "ru",
};
function counterReducer(state = initialState, action) {
  switch (action.type) {
    case "basket/add":
      return { ...state, basket: action.payload };
    case "basket/remove":
      return { ...state, basket: action.payload };
    case "lang/switch":
      return { ...state, lang: action.payload };
    default:
      return state;
  }
}

const persistConfig = {
  key: "root",
  storage,
};

const persistedReducer = persistReducer(persistConfig, counterReducer);

export let store = createStore(persistedReducer);
export let persistor = persistStore(store);

export const swithLanguage = (lang) => {
  return store.dispatch({
    type: "lang/switch",
    payload: lang,
  });
};

export const addItem = (item) => {
  const newBasket = [...store.getState().basket];
  const filtredItems = newBasket.findIndex((product) => product.id === item.id);
  if (filtredItems !== -1) {
    newBasket[filtredItems].count = newBasket[filtredItems].count + 1;

    const payload = newBasket;
    return store.dispatch({
      type: "basket/add",
      payload,
    });
  } else {
    item.count = 1;

    return store.dispatch({
      type: "basket/add",
      payload: [...newBasket, item],
    });
  }
};

export const deleteItem = (itemId) => {
  const newBasket = [...store.getState().basket];
  const filtredItems = newBasket.findIndex((product) => product.id === itemId);
  if (filtredItems > 0) {
    if (newBasket[filtredItems].count - 1 <= 0) {
      return store.dispatch({
        type: "basket/remove",
        payload: store.getState().basket.filter((item) => item.id !== itemId),
      });
    }
    newBasket[filtredItems].count = newBasket[filtredItems].count - 1;
    const payload = newBasket;
    return store.dispatch({
      type: "basket/add",
      payload,
    });
  } else {
    return store.dispatch({
      type: "basket/remove",
      payload: store.getState().basket.filter((item) => item.id !== itemId),
    });
  }
};
