import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { store } from "../../../store";

export const Card = ({ data }) => {
  const [currentLang, setCurrentLang] = useState();
  const currentLangFromState = store.getState().lang;

  useEffect(() => {
    setCurrentLang(currentLangFromState);
  }, [currentLangFromState]);

  return (
    <div className="col-lg-3 col-md-4 col-sm-6">
      <div className="sigma_product">
        <div className="sigma_product-thumb">
          <Link to={`/product-details:${data?.id}`}>
            <img src={`${data?.photo}`} alt="img" />
          </Link>
        </div>
        <div className="sigma_product-body">
          <h5 className="sigma_product-title">
            <Link to={`/product-details:${data?.id}`}>
              {currentLang === "ru" ? data?.titleru : data.titleua}
            </Link>
          </h5>
          <div className="sigma_product-price">
            <span>{data?.salePrice} грн</span>
            <span>{data?.sale} грн</span>
          </div>
        </div>
      </div>
    </div>
  );
};
