import React, { useEffect, useState } from "react";
import { useToasts } from "react-toast-notifications";
import Skeleton from "react-loading-skeleton";
import { useHistory, useParams } from "react-router";
import { Slider } from "../../components/ProductDetails/Slider";
import { getProducts } from "../../firebase";
import { addItem, deleteItem, store } from "../../store";
export const ProductDetails = () => {
  const { addToast } = useToasts();

  const [product, setProduct] = useState({});
  const [itemCount, setItemCount] = useState(0);
  const [showDescription, toggleShowDescription] = useState(false);
  const basketState = store.getState().basket;
  const { id } = useParams();
  const history = useHistory();
  useEffect(async () => {
    if (id) {
      const data = await getProducts(id);
      setProduct(data[0]);
    }
  }, []);
  useEffect(() => {
    const state = [...basketState];
    const item = state.find((item) => `:${item.id}` === id);
    item?.count > 0 ? setItemCount(item.count) : setItemCount(1);
  }, [basketState]);
  const addItemtoBasket = () => {
    product && addItem(product);
    addToast(currentLang === "ru" ? "В корзине !" : "У кошику !", {
      appearance: "success",
      autoDismiss: true,
    });
  };

  const hadnleAddProduct = () => {
    addItemtoBasket();
    setItemCount(itemCount + 1);
  };
  const handleRemoveProduct = () => {
    deleteItem(product.id);
    itemCount > 0 && setItemCount(itemCount - 1);
    addToast(currentLang === "ru" ? "Удалено !" : "Видалено !", {
      appearance: "error",
      autoDismiss: true,
    });
  };

  const handleButtonClick = () => {
    const hasItem = basketState.find((item) => item.id === id);
    console.log(basketState, hasItem);
    !hasItem && hadnleAddProduct();

    history.push("/checkout");
  };
  const currentLang = store.getState().lang;

  function relDiff(a, b) {
    return 100 * Math.abs((a - b) / ((a + b) / 2));
  }
  return (
    <>
      <div className="section-padding pb-0">
        <div className="container">
          <div className="row">
            <div className="col-lg-5">
              {product?.photo && (
                <Slider photo={product?.photo} data={product?.dopPhoto} />
              )}
            </div>
            <div className="col-lg-7">
              <div className="shop-detail-content mt-5 mt-lg-0">
                <h3 className="product-title m-0">
                  {product && currentLang === "ru" ? (
                    product.titleru
                  ) : product && currentLang === "ua" ? (
                    product.titleua
                  ) : (
                    <Skeleton height={30} />
                  )}
                </h3>

                <div className="sigma_product-price">
                  {product?.salePrice ? (
                    <span>{product.salePrice} грн</span>
                  ) : (
                    <Skeleton height={80} />
                  )}
                  <div className="saleBlock">
                    {product?.salePrice && product?.sale && (
                      <span className="persentSale">
                        {"sale "}
                        {parseInt(
                          relDiff(
                            parseFloat(product.sale),
                            parseFloat(product.salePrice)
                          )
                        )}
                        %
                      </span>
                    )}
                    {product?.sale && <span>{product.sale} грн</span>}
                  </div>
                </div>
                <div className="shop-detail-inner">
                  <div className="availability-box">
                    <div className="d-inline-block other-info">
                      <h6>
                        {currentLang === "ru" ? "Наличие" : "Наявнiсть"}:
                        {product?.availability ? (
                          <span className="text-success ml-2">
                            {currentLang === "ru" ? "В наличии" : "В наявностi"}
                          </span>
                        ) : (
                          <Skeleton />
                        )}
                      </h6>
                    </div>
                    <div className="ml-2 d-inline-block other-info">
                      <h6>
                        {currentLang === "ru" ? "Код товара" : "Код товару"} :
                        <span className="grey ml-2">{product?.id}</span>
                      </h6>
                    </div>
                  </div>
                  <div>
                    {" "}
                    <h4 className="kharakteristiki-title">Характеристики</h4>
                    <div className="kharakteristiki-list">
                      <div className="kharakteristiki-item">
                        <div className="kharakteristiki-item-title">
                          {currentLang === "ru"
                            ? "Как использовать"
                            : "Як використовувати"}
                        </div>
                        {product?.useru ? (
                          <div className="kharakteristiki-item-subtitle">
                            {currentLang === "ru"
                              ? product.useru
                              : product.useua}
                          </div>
                        ) : (
                          <Skeleton height={30} />
                        )}
                      </div>
                      <div className="kharakteristiki-item">
                        <div className="kharakteristiki-item-title">
                          {currentLang === "ru"
                            ? "Производство"
                            : "Виробництво"}
                        </div>
                        {product?.productionru ? (
                          <div className="kharakteristiki-item-subtitle">
                            {currentLang === "ru"
                              ? product.productionru
                              : product.productionua}
                          </div>
                        ) : (
                          <Skeleton height={30} />
                        )}
                      </div>
                      <div className="kharakteristiki-item">
                        <div className="kharakteristiki-item-title">
                          {currentLang === "ru" ? "Вес" : "Вага"}
                        </div>
                        {product?.weightru ? (
                          <div className="kharakteristiki-item-subtitle">
                            {currentLang === "ru"
                              ? product.weightru
                              : product.weightua}
                          </div>
                        ) : (
                          <Skeleton height={30} />
                        )}
                      </div>
                      <div className="kharakteristiki-item">
                        <div className="kharakteristiki-item-title">
                          {currentLang === "ru"
                            ? "Главные ингридиенты"
                            : "Головні інгредієнти"}
                        </div>

                        {product?.ingridientru ? (
                          <div className="kharakteristiki-item-subtitle">
                            {currentLang === "ru"
                              ? product.ingridientru
                              : product.ingridientua}
                          </div>
                        ) : (
                          <Skeleton height={30} />
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="quantity-cart d-sm-flex align-items-center">
                    {product?.sale ? (
                      <div className="quantity-box">
                        <button
                          onClick={product?.sale && handleRemoveProduct}
                          type="button"
                          className="minus-btn"
                        >
                          <i className="fa fa-minus"></i>
                        </button>
                        <input
                          type="text"
                          className="input-qty"
                          name="name"
                          value={itemCount}
                        />
                        <button
                          onClick={product?.sale && hadnleAddProduct}
                          type="button"
                          className="plus-btn"
                        >
                          <i className="fa fa-plus"></i>
                        </button>
                      </div>
                    ) : (
                      <Skeleton height={50} width={170} />
                    )}
                    <div className="cart-btn">
                      {product?.sale ? (
                        <button
                          className="btn saleButton"
                          onClick={handleButtonClick}
                        >
                          {currentLang === "ru" ? "В корзину" : "У кошик"}
                        </button>
                      ) : (
                        <Skeleton height={40} width={150} />
                      )}
                    </div>
                  </div>
                  <h4
                    onClick={() => toggleShowDescription(!showDescription)}
                    className="descr-title"
                  >
                    {currentLang === "ru" ? "Полное описание" : "Повний опис"}
                    <span> &#8595;</span>
                  </h4>
                  {showDescription && (
                    <div className="short-descr">
                      {product?.descriptionru ? (
                        <p>
                          {currentLang === "ru"
                            ? product.descriptionru
                            : product.descriptionua}
                        </p>
                      ) : (
                        <Skeleton height={90} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
