import React, { useEffect, useState } from "react";
import { getProducts } from "../../../firebase";
import { store } from "../../../store";
import { Card } from "./Card";
import { CardSkeleton } from "./CardSkeleton";

export const CardsBlock = () => {
  const [productData, setData] = useState([{}]);
  const [lang, setLang] = useState("");

  useEffect(async () => {
    const res = await getProducts();
    await setData(res);
  }, []);
  const currentLang = store.getState().lang;

  useEffect(() => {
    setLang(currentLang);
  }, [currentLang]);
  return (
    <div className="section-padding-2 pt-0">
      <div className="container">
        <div className="section-header text-center">
          <h5 id="shop">{lang === "ru" ? "Товары" : "Товари"} </h5>
          <div className="heading_arrow">
            <span className="dots_div"></span>
            <span className="s"></span>
            <span className="dots_div"></span>
          </div>
          <h2>
            {lang === "ru" ? "Последние поступления" : "Останнi надходження"}
          </h2>
        </div>
        <div className="row">
          {productData && productData.length > 1 ? (
            productData
              .sort((a, b) => a.position - b.position)
              .map((item, index) => {
                return <Card key={index} data={item} />;
              })
          ) : (
            <>
              <CardSkeleton /> <CardSkeleton />
              <CardSkeleton />
              <CardSkeleton />
            </>
          )}
        </div>
      </div>
    </div>
  );
};
