import React from "react";
import { store } from "../../store";

export const CheckoutItem = ({ data }) => {
  const currentLang = store.getState().lang;
  return (
    <tr>
      <td data-title="Product">
        <div className="andro_cart-product-wrapper">
          <div className="andro_cart-product-body">
            <h6>
              <a href="#">
                {currentLang === "ru" ? data.titleru : data.titleua}
              </a>
            </h6>
            <p>
              {data.count} {currentLang === "ru" ? "Кол-во" : "Кiль-сть"}
            </p>
          </div>
        </div>
      </td>
      <td data-title="Quantity">x{data.count}</td>
      <td data-title="Total">
        <strong>{parseFloat(data.salePrice.toString()) * data.count}</strong>
      </td>
    </tr>
  );
};
