import React, { useEffect, useState } from "react";
import Skeleton from "react-loading-skeleton";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";

export const Slider = (props) => {
  const { data, photo } = props;
  const [mainPhoto, setMainPhoto] = useState("");
  const [smallPhotos, setSmallPhotos] = useState();
  const [openLightBox, toggleOpenLighbox] = useState(false);

  useEffect(() => {
    photo && setMainPhoto(photo);
  }, [photo]);

  useEffect(() => {
    data && data.length > 0 && setSmallPhotos(data);
  }, [data]);

  const onSmallPhotoClick = (photo) => {
    const newSmallPhotos = smallPhotos.filter((item) => item !== photo);
    newSmallPhotos.push(mainPhoto);
    setSmallPhotos(newSmallPhotos);
    setMainPhoto(photo);
  };
  return (
    <>
      {openLightBox && (
        <Lightbox
          mainSrc={mainPhoto}
          onCloseRequest={() => toggleOpenLighbox(false)}
        />
      )}
      <div className="sigma_product-slider-1">
        <div
          style={{ cursor: "pointer" }}
          className="item"
          onClick={() => toggleOpenLighbox(true)}
        >
          {mainPhoto ? (
            <img src={mainPhoto} alt="img" />
          ) : (
            <Skeleton height={300} />
          )}
        </div>
      </div>
      <div className="sliderWrapper">
        {smallPhotos &&
          smallPhotos.length > 0 &&
          smallPhotos.map((slide, index) => (
            <div
              key={index}
              onClick={() => onSmallPhotoClick(slide)}
              className="slideWrapper"
            >
              <img className="sliderItem" src={slide} />
            </div>
          ))}
      </div>
    </>
  );
};
